.search_bar {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 30px;
}

.input {
  color: gray;
  padding: 15px;
  border: 1px solid rgb(0, 220, 0);
  border-radius: 15px;
  outline: none;

  &:focus {
    border: 1px solid green;
  }
}

.button {
  border: 1px solid salmon;
  background-color: #fff;
  color: salmon;
  border-radius: 15px;
  
  &:hover {
    border: 1px solid #fff;
    background-color: salmon;
    color: #fff;
  }

  &:active {
    border: 1px solid rgb(0, 220, 0);
    background-color: #fff;
    color: gray;
  }
}